<template>
  <div class="hello container">
    <h1>{{ msg }}</h1>

    <div class="vld-parent">
      <loading :active.sync="isLoading" :is-full-page="fullPage" :opacity="0.8">
        <div slot="before" class="loader_text">
          Scraping in Progress, data will populate soon...
        </div>
      </loading>
    </div>

    <form @submit.prevent="get_data">
      <div class="form-group">
        <label for="exampleInputEmail1">Search Param 1</label>
        <input
          placeholder="Query"
          type="text"
          class="form-control"
          id="exampleInputEmail1"
          v-model="q1"
        />
      </div>
      <div class="form-group">
        <label for="exampleInputPassword1">Search Param 2</label>
        <input
          type="text"
          placeholder="Query"
          class="form-control"
          id="exampleInputPassword1"
          v-model="q2"
        />
      </div>
      <button type="submit" class="btn btn-primary">Submit</button>
    </form>

    <!-- SCraped data -->

    <div
      v-if="
        data !== null && table1 && table2 && data.headers[0] && data.headers[1]
      "
      class="row pt-6 mt-5"
    >
      <div class="col-sm-6">
        <div class="card">
          <div class="card-body">
            <h6 class="card-title mb-0 pb-0">{{ data.headers[0] }}</h6>
            <ul>
              <li v-for="(item, i) in table1" :key="i" :id="'table1item' + i">
                {{ item }}
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div class="col-sm-6">
        <div class="card">
          <div class="card-body">
            <h6 class="card-title">{{ data.headers[1] }}</h6>
            <ul>
              <li v-for="(item, i) in table2" :key="i">{{ item }}</li>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <div
      v-if="data !== null && table3 && data.headers[2]"
      class="row pt-6 mt-5 mb-5"
    >
      <div class="col-sm-6">
        <div class="card">
          <div class="card-body">
            <h6 class="card-title">{{ data.headers[2] }}</h6>
            <ul>
              <li v-for="(item, i) in table3" :key="i">{{ item }}</li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Loading from "vue-loading-overlay";
import _ from "lodash";
import "vue-loading-overlay/dist/vue-loading.css";

export default {
  name: "Search",
  data: function() {
    return {
      q1: null,
      q2: null,
      data: null,
      error: null,
      isLoading: false,
      fullPage: true
    };
  },
  components: {
    Loading
  },
  props: {
    msg: String
  },
  computed: {
    table1: function() {
      let values = _.filter(this.data.table1_values, o => {
        return o !== "undefined";
      });
      return values;
    },
    table2: function() {
      let values = _.filter(this.data.table2_values, o => {
        return o !== "undefined";
      });
      return values;
    },
    table3: function() {
      let values = _.filter(this.data.table3_values, o => {
        return o !== "undefined";
      });
      return values;
    }
  },
  methods: {
    get_data() {
      if (this.q1 !== null && this.q2 !== null) {
        this.isLoading = true;

        axios
          .get("api/search", {
            params: {
              symbol: this.q1,
              period: this.q2
            },
            proxy: {
              host: "http://localhost:",
              port: 5000
            }
          })
          .then(response => {
            this.data = response.data;
            this.isLoading = false;
          })
          .catch(function(error) {
            if (error) {
              console.log(error);
            }
          })
          .finally(function() {
            // this.isLoading = false;
          });
      } else {
        this.$alert("Please Input two Queries to initiate the scraping");
      }
    }
  }
};
</script>

